.App {
  text-align: center;
  background-color: #282c34;
  height: 100vh;
  font-size: 2vmin;
  color: white;
  position: relative;
}

header{
  height:100vh;
}

footer{
  color: black;
}

/* Mobile adaption: Force oritention to landscape */

#require-horizental {
  width: 0;
  height: 0;
}

@media screen and  (orientation:  portrait){

  #require-horizental {
    background-color: white;
    background-image: url(./horizental.gif);
    background-size:contain;
    background-position:center;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 99;
  }
}
